import {
    encodeAES,
    decodeAES
} from "@/utils/other/index"
import {
    app
} from "@/main";
const state = {
    userInfo: initInfo,
    lang: "",
    userZone: "",
};

const initInfo = {
    id: "",
    email: "",
    name: "",
    avatarPath: "",
    token: "",
    expirationDate: ""
}

const actions = {
    login(context, obj) {
        localStorage.setItem("localUserInfo", encodeAES(JSON.stringify(obj)));
        context.commit('UPDATE_USER_INFO', obj);
        app.$router.push({
            path: "/admin/home"
        });
    },
    logout(context) {
        localStorage.removeItem("localUserInfo");
        context.commit('DELETE_USER_INFO');
        app.$router.push({
            path: "/login"
        });
    }
};
const mutations = {
    UPDATE_USER_INFO(state, obj) {
        state.userInfo = obj;
    },
    DELETE_USER_INFO(state) {
        state.userInfo = initInfo;
    },
};
const getters = {
    getUserInfo(state) {
        let userInfo = state.userInfo;
        if (!userInfo && localStorage.getItem('localUserInfo')) {
            userInfo = decodeAES(localStorage.getItem('localUserInfo'));
        }
        return userInfo
    }
};

export default {
    state,
    actions,
    mutations,
    getters
};