import Vue from 'vue'
import VueRouter from 'vue-router'
import AdminLayout from '@/layout/AdminLayout.vue'
import LoginLayout from '@/layout/LoginLayout.vue'
import Home from '@/views/admin/Home.vue'
import MyTable from '@/views/admin/MyTable.vue'
import About from '@/views/About.vue'
import Demo from '@/views/Demo.vue'
import {
  getStorage
}
from '@/utils/localstorage'

Vue.use(VueRouter)


//判別是否有權限
let getToken = routerName => {
  const userInfo = getStorage("localUserInfo")
  return userInfo.token;
};

//判別是否有權限
let checkPermission = routerName => {
  return true;
};

// 中間層檢查token 判別權限
let checkToken = (to, from, next) => {
  let token = getToken();
  let permission = checkPermission(to.name);
  switch (to.name) {
    case "login":
      token && next("/");
      token || next();
      break;
    case "home":
      token && next();
      token || next("/login");
      break;
    default:
      if (token && permission) {
        next();
      } else if (token && !permission) {
        console.error("routerCheck error: 該頁尚無權限，請確認")
        next("/");
      } else {
        next("/login");
      }
  }
};


const routes = [{
  path: '*', // 不存在的頁面自動導到登入頁
  redirect: '/Login'
}, {
  path: '/login',
  name: 'loginRoot',
  component: LoginLayout,
}, {
  path: '/about',
  name: 'about',
  component: About,
}, {
  path: '/demo',
  name: 'demo',
  component: Demo,
}, {
  path: '/admin',
  name: 'admin',
  component: AdminLayout,
  children: [{
    path: 'home',
    name: 'home',
    beforeEnter: checkToken,
    component: Home
  }, {
    path: 'table',
    name: 'table',
    beforeEnter: checkToken,
    component: MyTable
  }]
}]


const router = new VueRouter({
  mode: 'history',
  // base: process.env.BASE_URL,
  routes
})

export default router